export interface IMarkerProps {
    position: number[],
    event: string,
    year: string
}

function MyMarker(props: IMarkerProps) {
    const { position, event, year } = props;

    return (
        <Marker position={position}>
            <Tooltip permanent>
                {event} <br /> {year}
            </Tooltip>
        </Marker>
    )
}

function getMarkers(markers: IMarkerProps[]) {
    console.log(markers)
    return markers.map(marker => {
        return <MyMarker {...markers} />
    }
    )
}


export default function About() {
    const [state, setState] = React.useState(
        {
            lat: 38.75,
            lng: -9.29,
            zoom: 5,
        }
    )
    const position = [state.lat, state.lng]
    console.log(position)
    const markers = getMarkers(markerData);

    return (
        <Layout>
            <AboutText />
            <br />
            <TitleBanner title="MY JOURNEY" />
            <br />
            <div id="container" className="leaftlet-container" >
                <link rel="stylesheet" href="https://unpkg.com/leaflet@1.4.0/dist/leaflet.css" />
                <Map style={{ height: "600px" }} center={position} zoom={state.zoom}>
                    <TileLayer
                        attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    />
                    {markers}
                </Map>
            </div>
        </Layout>)
}